/* Profile page styles */
.profile-root{ font-family:Inter, system-ui, Arial; }
.profile-header{ display:flex; justify-content:space-between; align-items:center; gap:12px; }
.profile-meta{ display:flex; gap:12px; align-items:center; }
.avatar{ width:72px; height:72px; border-radius:999px; background:#ff7a2d; color:#fff; display:flex; align-items:center; justify-content:center; font-weight:800; font-size:26px; }
.p-name{ font-weight:800; font-size:18px; }
.p-email{ color:#64748b; font-size:13px; }
.avatar img{ width:100%; height:100%; object-fit:cover; display:block; }
.profile-stats{ display:flex; gap:18px; }
.profile-stats div{ text-align:center; background:linear-gradient(180deg,#fff,#fafafa); padding:10px 14px; border-radius:10px; box-shadow:0 10px 20px rgba(2,6,23,0.04); }
.profile-tabs{ margin-top:18px; display:flex; gap:8px; }
.profile-tabs button{ padding:8px 12px; border-radius:8px; border:1px solid rgba(15,23,42,0.06); background:transparent; cursor:pointer; }
.profile-tabs button.active{ background:linear-gradient(90deg,#ff7a2d,#ff3b7a); color:#fff; border:0; }
.profile-body{ margin-top:18px; }
.rl-grid{ display:grid; grid-template-columns: repeat(auto-fit, minmax(220px, 1fr)); gap:16px; }

.activity-list{ display:flex; flex-direction:column; gap:8px; margin-top:8px; }
.activity-item{ background:#fff; border-radius:8px; padding:8px 10px; box-shadow:0 6px 18px rgba(2,6,23,0.04); font-size:14px; }
