:root{
  --accent: #ff7a2d;
  --muted: #6b7280;
  --card-bg: #fff;
  --container-width: 1100px;
}

.nud-root { font-family: Inter, system-ui, Arial, sans-serif; color:#0f172a; }

/* Topbar */
.nud-topbar{
  display:flex;
  justify-content:space-between;
  align-items:center;
  padding:14px 24px;
  background:#fff;
  border-bottom:1px solid #eef2f7;
}
.nud-brand{ font-weight:800; font-size:18px; margin-right:18px; }
.nud-nav a{ margin-right:14px; color:#334155; text-decoration:none; font-weight:600; }
.nud-nav a.active{ color:var(--accent); }
.nud-right{ display:flex; align-items:center; gap:12px; }
.nud-user{ font-weight:600; color:#0f172a; }
.nud-logout{ background:transparent; border:1px solid rgba(15,23,42,0.06); padding:8px 10px; border-radius:8px; cursor:pointer; }

/* Hero banner */
.nud-hero{
  background-image: linear-gradient(rgba(0,0,0,0.25), rgba(0,0,0,0.25)), url('../assets/kitchen.jpg');
  background-size:cover;
  background-position:center;
  padding:96px 0; /* increased height */
  color:#fff;
}
.nud-hero-inner{ max-width:var(--container-width); margin:0 auto; padding:0 20px; }
.nud-hero h1{ margin:0; font-size:36px; font-weight:800; color:var(--accent); }

/* Stats */
.container{ max-width:var(--container-width); margin:0 auto; padding:28px 20px; }
.nud-stats{ display:flex; gap:18px; margin-top:-30px; }
.stat-card{
  background:var(--card-bg);
  padding:18px;
  border-radius:10px;
  flex:1;
  text-align:center;
  box-shadow: 0 8px 24px rgba(16,24,40,0.06);
}
.stat-num{ font-size:28px; font-weight:800; color:var(--accent); }
.stat-label{ margin-top:6px; color:var(--muted); font-size:13px; }

/* Empty state */
.nud-empty{ display:flex; align-items:center; justify-content:center; padding:80px 20px 120px; }
.nud-empty-card{
  width:100%;
  max-width:720px;
  background:#fff;
  border-radius:12px;
  padding:36px;
  text-align:center;
  box-shadow: 0 18px 48px rgba(16,24,40,0.08);
  border: 1px solid rgba(16,24,40,0.04);
}
.nud-empty-card h2{ margin:0 0 8px 0; font-size:22px; }
.nud-empty-card p{ margin:0 0 20px 0; color:var(--muted); }
.nud-actions{ display:flex; gap:12px; justify-content:center; margin-top:12px; }
.btn-primary{
  background:var(--accent);
  color:#fff;
  padding:12px 18px;
  border-radius:10px;
  border:none;
  font-weight:700;
  cursor:pointer;
}
.btn-secondary{
  background:transparent;
  color:var(--accent);
  padding:10px 16px;
  border-radius:10px;
  border:1px solid rgba(255,122,45,0.12);
  text-decoration:none;
  display:inline-flex;
  align-items:center;
}

/* Responsive */
@media (max-width:900px){
  .nud-stats{ flex-direction:column; }
  .nud-hero h1{ font-size:26px; }
  .nud-topbar{ padding:12px 14px; }
  .nud-empty-card{ padding:22px; }
}