:root{
  --accent:#ff7a2d;
  --muted:#dbe6db;
  --overlay: rgba(0,0,0,0.36);
}

.landing-hero{
  min-height:100vh;
  display:flex;
  flex-direction:column;
  /* debug fallback: show content without background image */
  background-image: none !important;
  background-color: #f6f6f6 !important;
  color: #111 !important;
}

/* Top navigation */
.landing-nav{
  display:flex;
  justify-content:space-between;
  align-items:center;
  padding:20px 32px;
}
.brand{
  font-weight:700;
  font-size:20px;
}
.landing-nav nav {
  display:flex;
  gap:12px;
  align-items:center;
}
.nav-link{
  color:rgba(255,255,255,0.95);
  text-decoration:none;
  padding:6px 8px;
}
.btn{
  display:inline-block;
  text-decoration:none;
  padding:10px 16px;
  border-radius:10px;
  font-weight:700;
}
.btn-cta{
  background:var(--accent);
  color:#fff;
  box-shadow: 0 6px 18px rgba(255,122,45,0.18);
}

/* Hero layout */
.hero-content{
  display:flex;
  flex:1;
  gap:24px;
  align-items:center;
  padding:48px 8%;
}
.hero-left{
  width:52%;
  max-width:760px;
}
.tag{
  display:inline-block;
  background:rgba(255,255,255,0.08);
  padding:6px 10px;
  border-radius:999px;
  color:var(--muted);
  font-size:13px;
  margin-bottom:18px;
}
h1{
  font-size:56px;
  line-height:1.02;
  margin:0 0 18px 0;
  font-weight:800;
  color:#fff;
}
.accent{
  color:var(--accent);
}
.lead{
  color:rgba(255,255,255,0.92);
  max-width:620px;
  margin-bottom:22px;
  font-size:18px;
  color:rgba(255,255,255,0.9);
}
.hero-actions{
  display:flex;
  gap:12px;
  align-items:center;
}
.btn-primary{
  background:var(--accent);
  color:#fff;
  padding:12px 20px;
  border-radius:10px;
}
.btn-ghost{
  background:transparent;
  color:#fff;
  border:1px solid rgba(255,255,255,0.12);
  padding:10px 18px;
  border-radius:10px;
}

/* decorative right column */
.hero-right{
  flex:1;
  min-height:420px;
  border-radius:16px;
  box-shadow: inset 0 0 80px rgba(0,0,0,0.25);
  backdrop-filter: blur(2px);
  display:block;
}

/* mock cards collage inside hero-right */
.mock-cards { position: relative; width: 380px; height: 420px; margin-left: auto; }
.mock-card { position: absolute; border-radius: 12px; overflow: hidden; box-shadow: 0 12px 40px rgba(2,6,23,0.18); background:#fff; }
.mock-card img { width:100%; height:100%; object-fit:cover; display:block; }
.mock-meta { position:absolute; left:12px; bottom:12px; color:#fff; text-shadow:0 2px 8px rgba(0,0,0,0.6); }
.mock-cal { background:rgba(0,0,0,0.55); padding:6px 10px; border-radius:999px; font-weight:700; margin-right:8px; display:inline-block; }
.mock-title { margin-top:6px; font-weight:700; font-size:13px; }

.mock-card-large { width:100%; height:100%; left:0; top:0; transform: rotate(-2deg); }
.mock-card-medium { width:260px; height:180px; right:-10px; top:40px; transform: rotate(4deg); }
.mock-card-small { width:200px; height:140px; right:30px; bottom:-10px; transform: rotate(-3deg); }

@media (max-width:900px){
  .hero-content{flex-direction:column; padding:36px 6%;}
  .hero-left{width:100%;}
  .hero-right{display:none;}
  h1{font-size:36px;}
  .mock-cards { display:none; }
}